import React from "react";
import { Link } from "react-router-dom";
import { request } from "../api/api";
import { Addfavorite, Delete } from "../api/favorite";
import Cookie from "js-cookie";
class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: {
        articles: [],
      },
      articlesCount: "",
      index: 0,
      favoritedisabled: false,
    };
  }

  componentDidMount() {
    request({
      method: "GET",
      url: "/api/articles?limit=10",
      params: {},
    }).then((res) => {
      // console.log(res);
      this.setState({
        data: res.data,
        articlesCount: res.data.articlesCount / res.data.articles.length,
      });
    });
  }
  click(e) {
    this.setState(
      {
        index: e.target.text - 1,
      },
      () => {
        request({
          method: "GET",
          url:
            "/api/articles?limit=10&offset=" +
            this.state.index * this.state.data.articles.length,
        }).then((res) => {
          this.setState({
            data: res.data,
          });
        });
      }
    );
  }
  async favorite(data) {
    console.log(data);
    if (Cookie.get("auth")) {
      await this.setState({ favoritedisabled: true });
      if (data.favorited) {
        await Delete(data).then((res) => {
          //异步
          console.log("取消", res);
        });
        data.favorited = false;
        data.favoritesCount += -1;
        // 获取当前点击的评论是哪一条
        console.log(this.state.data.articles.indexOf(data));
        let key = this.state.data.articles.indexOf(data);
        this.state.data.articles[key] = data;
        await this.setState({
          //异步
          articles: this.state.data.articles,
        });
      } else {
        await Addfavorite(data).then((res) => {
          //异步
          console.log("添加", res);
        });
        data.favorited = true;
        data.favoritesCount += 1;

        let key = this.state.data.articles.indexOf(data);
        this.state.data.articles[key] = data;
        await this.setState({
          //异步
          articles: this.state.data.articles,
        });
      }
      await this.setState({ favoritedisabled: false });
    } else {
      this.props.history.push("/login");
    }
  }

  render() {
    return (
      <div className="home-page">
        <div className="banner">
          <div className="container">
            <h1 className="logo-font">conduit</h1>
            <p>A place to share your knowledge.</p>
          </div>
        </div>

        <div className="container page">
          <div className="row">
            <div className="col-md-9">
              <div className="feed-toggle">
                <ul className="nav nav-pills outline-active">
                  <li className="nav-item">
                    <Link className="nav-link disabled" to="/">
                      Your Feed
                    </Link>
                  </li>
                  <li className="nav-item">
                    <Link className="nav-link active" to="/">
                      Global Feed
                    </Link>
                  </li>
                </ul>
              </div>
              {this.state.data.articles.map((item, index) => {
                return (
                  <div className="article-preview" key={index}>
                    <div className="article-meta">
                      <Link to="profile.html">
                        <img src={item.author.image} alt="" />
                      </Link>
                      <div className="info">
                        <Link to="/" className="author">
                          {item.author.username}
                        </Link>
                        <span className="date">{item.createdAt}</span>
                      </div>
                      <button
                        onClick={() => {
                          this.favorite(item);
                        }}
                        disabled={this.state.favoritedisabled}
                        className="btn btn-outline-primary btn-sm pull-xs-right"
                      >
                        <i className="ion-heart"></i> {item.favoritesCount}
                      </button>
                    </div>
                    <Link to="/" className="preview-link">
                      <h1>{item.title}</h1>
                      <p>{item.description}</p>
                      <span>Read more...</span>
                    </Link>
                  </div>
                );
              })}
              <ul className="pagination">
                {new Array(this.state.articlesCount)
                  .fill("")
                  .map((item, index) => {
                    return (
                      <li
                        className={
                          this.state.index === index
                            ? "page-item active"
                            : "page-item"
                        }
                        ng-class="{active: pageNumber === $ctrl.currentPage }"
                        ng-repeat="pageNumber in $ctrl.pageRange($ctrl.totalPages)"
                        ng-click="$ctrl.changePage(pageNumber)"
                        key={index}
                        onClick={this.click.bind(this)}
                      >
                        <Link className="page-link ng-binding" to="/">
                          {index + 1}
                        </Link>
                      </li>
                    );
                  })}
              </ul>
            </div>
            <div className="col-md-3">
              <div className="sidebar">
                <p>Popular Tags</p>
                <div className="tag-list">
                  <Link to="/" className="tag-pill tag-default">
                    programming
                  </Link>
                  <Link to="/" className="tag-pill tag-default">
                    javascript
                  </Link>
                  <Link to="/" className="tag-pill tag-default">
                    emberjs
                  </Link>
                  <Link to="/" className="tag-pill tag-default">
                    angularjs
                  </Link>
                  <Link to="/" className="tag-pill tag-default">
                    react
                  </Link>
                  <Link to="/" className="tag-pill tag-default">
                    mean
                  </Link>
                  <Link to="/" className="tag-pill tag-default">
                    node
                  </Link>
                  <Link to="/" className="tag-pill tag-default">
                    rails
                  </Link>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Home;
