import React from "react";
import { request } from "../api/api";
import Cookie from "js-cookie";
import Pubsub from "pubsub-js";

class A extends React.Component {
  constructor(prors) {
    super(prors);
    this.onChange = this.onChange.bind(this);
  }

  onChange(event) {
    var value = event.target.value;
    // console.log(value);
    //把username值传给Login组件 发布者
    Pubsub.publish("value", value);
  }
  render() {
    return (
      <div>
        <fieldset className="form-group">
          <input
            className="form-control form-control-lg"
            type="text"
            placeholder="Your Name"
            onChange={this.onChange}
          />
        </fieldset>
      </div>
    );
  }
}
class Login extends React.Component {
  constructor(props) {
    super(props);
    //为事件函数绑定this
    this.onsubmit = this.onsubmit.bind(this);
    this.onChange1 = this.onChange1.bind(this);
    this.onChange2 = this.onChange2.bind(this);
    //把表单中的input数据实现成响应式
    this.state = {
      username: "",
      email: "",
      password: "",
    };
  }

  componentDidMount() {
    Pubsub.subscribe("value", (msg, data) => {
      this.setState({
        username: data,
      });
    });
  }

  onsubmit(event) {
    //屏蔽form表单的提交事件
    event.preventDefault();
    // console.log('----');
    let user = {
      username: this.state.username ? this.state.username : "",
      email: this.state.email,
      password: this.state.password,
    };
    console.log(user.username);
    request({
      method: "POST",
      url: this.state.username ? "/api/users" : "/api/users/login",
      data: {
        user: user,
      },
    }).then((res) => {
      console.log(res);
      console.log(res.data.user.token);
      //将token存储到Cookie中
      Cookie.set("auth", res.data.user.token);
      Cookie.set("img", res.data.user.image);
      Cookie.set("name", res.data.user.username);

      // console.log('--',this);
      //跳转路由
      this.props.history.push("/");
      //发布
      Pubsub.publish("auth", res.data.user.token);
      Pubsub.publish("updata", { auth: true });
    });
  }

  onChange1(event) {
    // console.log(event.target.value);
    this.setState({
      email: event.target.value,
    });
  }
  onChange2(event) {
    // console.log(event.target.value);
    this.setState({
      password: event.target.value,
    });
  }

  isLogin() {
    //判断是注册还是登陆  register   Sign up  login   Sign in
    return this.props.match.path === "/login";
  }

  render() {
    // console.log(this.state);
    return (
      <div className="auth-page">
        <div className="container page">
          <div className="row">
            <div className="col-md-6 offset-md-3 col-xs-12">
              <h1 className="text-xs-center">
                {this.isLogin() ? "Sign in" : "Sign up"}
              </h1>
              <p className="text-xs-center">
                <a href="##">
                  {this.isLogin() ? "Need an account" : " Have an account"}
                </a>
              </p>
              <ul className="error-messages">
                <li>That email is already taken</li>
              </ul>
              <form onSubmit={this.onsubmit}>
                {this.isLogin() ? "" : <A />}
                <fieldset className="form-group">
                  <input
                    className="form-control form-control-lg"
                    type="text"
                    placeholder="Email"
                    onChange={this.onChange1}
                  />
                </fieldset>
                <fieldset className="form-group">
                  <input
                    className="form-control form-control-lg"
                    type="password"
                    placeholder="Password"
                    onChange={this.onChange2}
                  />
                </fieldset>
                <button className="btn btn-lg btn-primary pull-xs-right">
                  {this.isLogin() ? " Sign in" : "  Sign up"}
                </button>
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Login;
