import React from "react";
import { NavLink } from "react-router-dom";
import Cookie from "js-cookie";
import Pubsub from "pubsub-js";

class Header extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      auth: "",
    };
  }

  componentDidMount() {
    var that = this;
    //将Cookie中的auth  和   state中的auth 进行合并
    Pubsub.subscribe("auth", function (msg, data) {
      // console.log(msg,data);
      that.setState({
        auth: data,
      });
    });
    Pubsub.subscribe("remove", function (dat, obj) {
      that.setState(obj);
    });
  }

  render() {
    if (Cookie.get("auth")) {
      return (
        <div>
          <nav className="navbar navbar-light">
            <div className="container">
              <a className="navbar-brand" href="index.html">
                conduit
              </a>
              <ul className="nav navbar-nav pull-xs-right">
                <li className="nav-item">
                  <NavLink className="nav-link active" to="/">
                    Home
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink
                    className="nav-link"
                    to="/editor"
                    activeClassName="active"
                  >
                    <i className="ion-compose"></i>&nbsp;New Article
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink
                    className="nav-link"
                    to="/settings"
                    activeClassName="active"
                  >
                    <i className="ion-gear-a"></i>&nbsp;Settings
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink
                    className="nav-link ng-binding active"
                    ui-sref-active="active"
                    ui-sref="app.profile.main({ username: $ctrl.currentUser.username })"
                    to="/profile"
                    activeClassName="active"
                  >
                    <img
                      className="user-pic"
                      ng-src="https://z3.ax1x.com/2021/04/29/gFXnCF.png"
                      src={Cookie.get("img")}
                      alt=""
                    />
                    {Cookie.get("name")}
                  </NavLink>
                </li>
              </ul>
            </div>
          </nav>
        </div>
      );
    } else {
      return (
        <div>
          <nav className="navbar navbar-light">
            <div className="container">
              <a className="navbar-brand" href="index.html">
                conduit
              </a>
              <ul className="nav navbar-nav pull-xs-right">
                <li className="nav-item">
                  <NavLink className="nav-link active" to="/">
                    Home
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink
                    className="nav-link"
                    to="/login"
                    activeClassName="active"
                  >
                    Sign in
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink
                    className="nav-link"
                    to="/register"
                    activeClassName="active"
                  >
                    Sign up
                  </NavLink>
                </li>
              </ul>
            </div>
          </nav>
        </div>
      );
    }
  }
}

export default Header;
