import { request } from "./api";
import Cookie from "js-cookie";
export let Addfavorite = (item) => {
  //   console.log("..........");
  var slug = item.slug;
  request.interceptors.request.use(
    function (config) {
      //携带token   保存到请求头
      //   console.log("config", Cookie.get("auth"));
      config.headers.Authorization = "Token " + Cookie.get("auth");
      return config;
    },
    function (error) {
      return Promise.reject(error);
    }
  );
  return request({
    method: "POST",
    url: `/api/articles/${slug}/favorite`,
  });
};
export let Delete = (item) => {
  //   console.log("..........");
  var slug = item.slug;
  request.interceptors.request.use(
    function (config) {
      config.headers.Authorization = "Token " + Cookie.get("auth");
      return config;
    },
    function (error) {
      return Promise.reject(error);
    }
  );
  return request({
    method: "DELETE",
    url: `/api/articles/${slug}/favorite`,
  });
};
