import React from "react";
import { request } from "../api/api";
import Cookie from "js-cookie";
class Editor extends React.Component {
  state = {
    title: "",
    about: "",
    article: "",
    enter: "",
  };
  handleTitle(e) {
    this.setState({ title: e.target.value });
  }
  handleAbout(e) {
    this.setState({ about: e.target.value });
  }
  handleArticle(e) {
    this.setState({ article: e.target.value });
  }
  handleEnter(e) {
    this.setState({ enter: e.target.value });
  }
  click() {
    if (this.state.title && this.state.about && this.state.article) {
      request.interceptors.request.use(
        function (config) {
          //携带token   保存到请求头
          //   console.log("config", Cookie.get("auth"));
          config.headers.Authorization = "Token " + Cookie.get("auth");
          return config;
        },
        function (error) {
          return Promise.reject(error);
        }
      );
      return request({
        method: "post",
        url: "/api/articles",
        data: {
          article: {
            title: this.state.title,
            description: this.state.about,
            body: this.state.article,
            tagList: this.state.enter,
          },
        },
      }).then((res) => {
        console.log(res);
        this.setState({ title: "", about: "", article: "", enter: "" });
        this.props.history.push("/article");
      });
    } else {
      return alert("前三个输入框不能为空");
    }
  }
  render() {
    return (
      <div className="editor-page">
        <div className="container page">
          <div className="row">
            <div className="col-md-10 offset-md-1 col-xs-12">
              <form>
                <fieldset>
                  <fieldset className="form-group">
                    <input
                      type="text"
                      value={this.state.title}
                      className="form-control form-control-lg"
                      placeholder="Article Title"
                      onChange={this.handleTitle.bind(this)}
                    />
                  </fieldset>
                  <fieldset className="form-group">
                    <input
                      type="text"
                      value={this.state.about}
                      className="form-control"
                      placeholder="What's this article about?"
                      onChange={this.handleAbout.bind(this)}
                    />
                  </fieldset>
                  <fieldset className="form-group">
                    <textarea
                      className="form-control"
                      rows="8"
                      value={this.state.article}
                      placeholder="Write your article (in markdown)"
                      onChange={this.handleArticle.bind(this)}
                    ></textarea>
                  </fieldset>
                  <fieldset className="form-group">
                    <input
                      type="text"
                      value={this.state.enter}
                      className="form-control"
                      placeholder="Enter tags"
                      onChange={this.handleEnter.bind(this)}
                    />
                    <div className="tag-list"></div>
                  </fieldset>
                  <button
                    className="btn btn-lg pull-xs-right btn-primary"
                    type="button"
                    onClick={this.click.bind(this)}
                  >
                    Publish Article
                  </button>
                </fieldset>
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Editor;
